<template>
  <div class="singlepage">
    <!-- Loading center -->
    <div v-if="loadnow" class="progressbar">
        Pokemon Loading
        <v-progress-linear indeterminate color="yellow darken-2"></v-progress-linear>
    </div>
    <!-- single page view  -->
    <div v-else class="d-flex mobileview" transition="fade-transition">
        <div class="img">
            <!-- pokemon image  -->
            <img :src="api.imageUrl + this.$route.params.id + '.png'" alt=""/>
        </div>
        <div class="text-left rightbox">
            <!-- go back link  -->
            <div class="gobacklink">
                <router-link to="/" >
                    <img src="../assets/left.png" alt="">
                </router-link>
            </div>
            <!-- pokemon name  -->
            <div class="pokemontitle">
                {{pokee.name}}
            </div>
            <!-- pokemon details  -->
            <div class="middlebox">
                <h3>Height: {{ pokee.height*0.1 }} meters</h3>
                <h3>Weight: {{ pokee.weight*0.1 }} kilograms</h3>
            </div>
            <div class="row">
                <div class="col-md-6">
                    <div class="middlebox">
                        <h2>Abilities</h2>
                        <v-chip
                            class="ma-2"
                            color="red"
                            text-color="white"
                            v-for="(ability, index) in pokee.abilities" :key="index"
                        >
                            {{ability.ability.name}}
                        </v-chip>
                    </div>
                </div>
                <div class="col-md-6">
                    <div class="middlebox">
                        <h2>Types</h2>
                        <v-chip
                            class="ma-2"
                            color="green"
                            text-color="white"
                            v-for="(type, index) in pokee.types" :key="index"
                        >
                            {{type.type.name}}
                        </v-chip>
                    </div>
                </div>
            </div>
            <div class="middlebox">
                <div v-for="(staa, index) in pokee.stats" :key="index">
                    <v-progress-linear
                        height="30px"
                        :value="staa.base_stat"
                        color="amber"
                        style="margin-bottom: 10px; font-weight: 700"
                    >
                        {{staa.stat.name}} - {{staa.base_stat}}%
                    </v-progress-linear>
                </div>
            </div>
        </div>
    </div>
  </div>
</template>

<script>
export default {
    name: 'singlepage',
    props: ['api'],
    data: () => ({
        pokee: [],
        loadnow: true,
    }),
    created(){
        setTimeout(() => {
            // getting the value from api 
            this.axios
                .get(this.api.apiUrl + '/' +this.$route.params.id) 
                .then( (res) => {
                    this.loadnow = false;
                    this.pokee = res.data;                
                });
        }, 3000);
    },
}
</script>

<style lang="scss" scoped>
    div{
        color: #fff;
    }
    .singlepage{
        width: 100%;
        height: 100%;
        background: #024775;
    }
    .pokemontitle{
        width: 100%;
        color: #fff;
        padding: 15px 0;
        font-size: 44px;
        text-transform: capitalize;
        font-weight: 700;
        letter-spacing: 1px;
        border-bottom: 1px solid white;
    }
    .d-flex{
        .img{
            width: 40%;
            padding: 20px;
            img{
                width: 100%;
            }
        }
        .rightbox{
            .gobacklink{
                position: absolute;
                top: 10px;
                left: 10px;
                a{
                    display: inline-block;
                    background: white;
                    border-radius: 5px;
                    text-align: center;
                    img{
                        margin-top: 5px;
                        width: 30px;
                        height: 30px
                    }
                }
            }
            width: 60%;
            padding: 20px;
            margin-left: 30px;
            .middlebox{
                padding: 20px 0;
                border-bottom: 1px solid #fff;
            }
        }
    }
    @media screen and (max-width: 500px) {
        .gobacklink{
            position: absolute;
            top: 10px;
            left: 10px;
            background: white;
            border-radius: 5px;
            text-align: center;
            img{
                margin-top: 5px;
                width: 30px;
                height: 30px
            }
        }
        .mobileview{
            flex-direction: column;
            .img{
                width: 100%;
            }
            .rightbox{
                margin-left: 0px;
                width: 100%;
            }

        }
    }
    .progressbar{
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 200px;
    }
</style>