<template>
  <div class="home">
    <div class="pokemon">
      <div class="titlehead">
        <h1>Pokedox</h1>
      </div>
    </div>
    <v-container>
      <div class="row">
        <div class="col-md-12">
          <!-- search field  -->
          <v-text-field
            label="Search Pokemon.."
            v-model="pokeman"
          ></v-text-field>

          <div class="row">
            <div class="col-md-3 col-lg-3 col-sm-4 col-12" v-for="(card, index) in filteredPokeman" :key="index">
              <v-card class="dockcard">
                <!-- pokemon cards  -->
                <div @click="pokecard(card.id)" >
                  <div class="titleofbox">
                    <!-- pokemon name  -->
                    <h3>
                      {{card.name}}
                    </h3>
                  </div>
                  <div class="avatarbox">
                      <!-- pokemon image -->
                      <img :src="api.imageUrl + card.id + '.png'" alt=""/>
                  </div>
                </div>
              </v-card>
            </div>
          </div>
        </div>
      </div>
    </v-container>
  </div>
</template>

<script>

export default {
  name: 'Home',
  props: ['api'],
  data: () => ({
    pokeman: '',
    cards: [],
  }),
  mounted(){
    // calling api
    this.axios
      .get(this.api.apiUrl + "?limit=100") 
      .then( (res) => {
        // pokemon data 
        res.data.results.forEach(pokemon => {
          pokemon.id = pokemon.url.split('/').filter(function(part){return !!part }).pop();
          // push into cards 
          this.cards.push(pokemon);
        });
      });
  },
  methods: {
    // go to the single page
    pokecard(id){
      this.$router.push('/pokemon/' + id)
    },
  },
  computed:{
    // get filtered pokemon 
    filteredPokeman: function(){
      return this.cards.filter((pokeman) => {
        return pokeman.name.match(this.pokeman);
      });
    }
  }
}
</script>
