  .pokemon{
    position: relative;
    width: 100%;
    background: #024775;
  }
  .titlehead{
    color: white;
    h1{
      font-size: 40px;
    }
  }
  .dockcard{
    cursor: pointer;
    transition: .3s;
    &:hover{
      background: #024775;
      img{
        transform: scale(1.3);
      }
    }
    .titleofbox{
      padding: 10px 0;
      width: 100%;
      background: #024775;
      h3{
        color: white;
        font-size: 18px;
        text-transform: capitalize;
      }
    }
    .avatarbox{
      width: 100%;
      img{
        width: 160px;
        transition: .3s;
      }
    }
  }
</style>